import { actions } from "mirrorx";
// services
import * as api from "./service";
// 
import { processData } from "utils";
import moment from 'moment';

/**
 *          btnFlag
 *          
 *          
 *          
 *          012 3
 *async loadList(param, getState) {
 *          rowData
*/


export default {
    //  Store 
    name: "translate",
    //  Model  state
    initialState: {
        rowData:{},
        showLoading:false,
        list: [],
        orderTypes:[],
        pageIndex:1,
        pageSize:10,
        totalPages:1,
        total:0,
        detail:{},
        searchParam:{},
        validateNum:99,//step

    },
    reducers: {
        /**
         *  Redux  Reducer
         * @param {*} state
         * @param {*} data
         */
        updateState(state, data) { //state
            return {
                ...state,
                ...data
            };
        }
    },
    effects: {
        /**
         * 
         * @param {*} param
         * @param {*} getState
         */
        async loadList(param, getState) {
            //  Loading 
            actions.translate.updateState({ showLoading:true })
            if(param){
                param.pageIndex = param.pageIndex ? param.pageIndex - 1 : 0;
                param.pageSize = param.pageSize ? param.pageSize : 10;
            } else {
                param = {}
            }
            //  getList 
            let res = processData(await api.getList(param));
            actions.translate.updateState({  showLoading:false })
            if (res) {
                if(res.content&&res.content.length){
                    for(let i=0;i<res.content.length;i++){
                        let temp = Object.assign({},res.content[i]);
                        res.content[i].key=i+1;
                        
                        res.content[i].chinese = temp.chinese+"";
                    
                        
                        res.content[i].traditional = temp.traditional+"";
                    
                        
                        res.content[i].propertyCode = temp.propertyCode+"";
                    
                        
                        res.content[i].english = temp.english+"";
                    
                    
                    
                        
                        res.content[i].french = temp.french+"";
                    
                    }
                }
                // console.log('res content',res.content);
                actions.translate.updateState({
                    list: res.content,
                    pageIndex:res.number + 1,
                    totalPages:res.totalPages,
                    total:res.totalElements
                });
            }
        },

        /**
         * getSelect
         * @param {*} param
         * @param {*} getState
         */
        getOrderTypes(param,getState){
            actions.translate.updateState({
            orderTypes:  [{
                "code":"0",
                "name":"D001"
            },{
                "code":"1",
                "name":"D002"
            },{
                "code":"2",
                "name":"D003"
            },{
                "code":"3",
                "name":"D004"
            }]
            })
        },

        /**
         * getSelecttable
         * @param {*} param
         * @param {*} getState
         */
        async saveList(param, getState) {
            let result = await api.saveList(param);
            return result;
        },
        /**
         * table
         * @param {*} id
         * @param {*} getState
         */
        async removeList(id, getState) {
            let result = await api.deleteList([{id}]);
            return result;
        },

        async delItem(param,getState){
            actions.translate.updateState({
              showLoading:true
            })
            let res=processData(await api.delTranslate(param.param),'');
            actions.translate.loadList();
        },

        async save(param,getState){//
            actions.translate.updateState({
              showLoading:true
            })
            let res = processData(await api.saveTranslate(param),'');
            console.log("",res);
            if(res){
               window.history.go(-1);
            }
            actions.translate.updateState({
                showLoading:false,

            });
        },

        async queryDetail(param,getState) {
            let {data:{detailMsg:{data:{content}}}}=await api.getDetail(param);
            return content[0];
        },

        
        async exportExcel(param) {
            api.exportExcel(param || {});
        },


    }
};


